@import "../vars.scss";
.breadcrumbs {
    margin: 1.5em 0;
    color: rgba($gray,.6);
    li{
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: 1.75em;
            &:before {
                content: 'â€”';
                position: absolute;
                top: .125em;
                right: 100%;
                margin-right: .625em;
                font-size: .875em;
                line-height: 157%;
            }
        }
        &:last-child{
            pointer-events: none;
            color: $gray;
        }
    }
    a,span{
        font-size: .875em;
        line-height: 157%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        ul{
            font-size: .86em;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        margin: 1em 0 .75em;
    }
}

