.guide-item{
  display: flex;
  align-items: center;
  padding: 1em;
  border-radius: 1.5em;
  background-color: $fon;
  img{
    width: 4em;
    margin-right: 1.5em;
  }
}

.delivery-partners{
  padding: 3.75em 0;
  max-width: 1920px;
  width: calc(100% - 1.75em);
  margin: 0 auto;
  overflow: hidden;
  background-color: $fon;
  &:hover .delivery-partners__items{
    animation-play-state: paused;
  }
  &__line{
    display: flex;
    gap: 7.5em;
    align-items: center;

  }
  &__items{
    gap: 7.5em;
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    animation: 20s slides infinite linear;
  }
  &__item{
    img{
      height: 2em;
      display: block;
    }
  }
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

.pick-up-info{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 38em) 36.5em;
    gap: 1.5em;
  }
}

.map{
  height: 33.75em;
  border-radius: 1.5em;
  overflow: hidden;
}

.pick-up-points{
  height: 33.75em;
  border-radius: 1.5em;
  background-color: $fon;
  padding: 2em 2em 0;
  .fg{
    .nice-select{
      padding-left: 1em;
      background-color: $white !important;
      height: 2.875em;
      border-radius: .5em;
    }
  }
  &__items{
    margin-top: 2em;
    overflow-y: auto;
    height: 26.875em;
    padding-bottom: 2em;
    padding-right: 1em;
    @include grid100gap(1.25em);
    @include scroll;
  }
}

.point-item{
  display: flex;
  align-items: flex-start;
  padding-bottom: 1.25em;
  border-bottom: $border;
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
  img{
    width: 1.5em;
    margin-right: .625em;
  }
}


@media screen and (max-width: 992px){
  .for-buyers{
    .h2{
      display: none;
    }
    &__grid{
      margin-top: 0;
    }
  }

  .delivery-partners{
    padding: 2.5em 0;
    width: 100%;
    &__line{
      gap: 4em;
    }
    &__items{
      gap: 4em;
      animation: 15s slides infinite linear;
    }
    &__item{
      img{
        height: 1.25em;
      }
    }
  }

  .pick-up-info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .map{
    height: 27.5em;
  }
}


@media screen and (max-width: 576px){
  .delivery-partners{
    padding: 2em 0;
    &__line{
      gap: 2.5em;
    }
    &__items{
      gap: 2.5em;
      animation: 15s slides infinite linear;
    }
    &__item{
      img{
        height: 1em;
      }
    }
  }

  .pick-up-info{
    &__grid{
      gap: .75em;
    }
  }

  .map{
    border-radius: 1em;
  }

  .pick-up-points{
    border-radius: 1em;
    padding: 1.5em 1.5em 0;
    &__items{
      margin-top: 1.5em;
      height: 27.375em;
      padding-bottom: 1.5em;
    }
  }
}
