.fg,
.m-select{
  position: relative;
  label{
    display: block;
    margin-bottom: 1em;
  }
}

.fg {
  margin: 0;
  select,
  .nice-select,
  input,
  textarea{
    border-radius: 1em;
    border: none;
    padding: 0 1.5em;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    background-color: $fon;
    color: $black;
    &::placeholder {
      color: $gray;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    white-space: nowrap;
    padding-right: 3em;
    float: unset;
    background-color: $fon !important;
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: 1em;
      height: 1em;
      background-image: url(../img/select-arrow.svg),url(../img/select-arrow2.svg);
      @include bgImgSize(100%);
      background-size: 100% auto, 0 auto;
      right: .875em;
      top: 50%;
      transform: translateY(-50%);
      position: absolute;
    }
    &.open{
      &:before{
        background-size: 0 auto,100% auto;
      }
    }
    .current{
      font-size: 1em;
      line-height: 150%;
      width: 100%;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .list{
      margin-top: .375em;
      border-radius: .375em;
      box-shadow: 0px 6px 16px 0px rgba(20, 20, 20, 0.10);
      border: none;
      background: #FFF;
      max-height: 20.75em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
      padding: .375em;
    }
    .option{
      font-size: 1em;
      line-height: 150%;
      padding: .5em;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      border-radius: .25em;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $fon;
        font-weight: 400;
        color: $black;
      }
    }
  }
  select{
    padding-right: 3em;
    @include removeSelectArrow;
    background-image: url(../img/select-arrow.svg);
    background-size: 1.125em auto;
    background-position: right 1em center;
    background-repeat: no-repeat;
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 7.28em;
    resize: none;
  }
}

.fg-white{
  .nice-select,
  textarea,
  input{
    background-color: $white;
    border-color: $white;
  }
}

.order-delivery__radio,
.select-option,
.sort-item,
.m-switch,
.btn-radio,
.color-radio,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2em;
    min-height: 1.375em;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 1.375em;
      height: 1.375em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      color: $primary;
      position: relative;
      z-index: 2;
    }
  }
  &._start{
    label{
      display: block;
      &:before{
        top: 0;
        transform: unset;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 0 auto, 100% auto;
      }
    }
  }
  label {
    &:before {
      background-image: url(../img/check.svg), url(../img/check2.svg);
      @include bgImgSize(100%);
      background-size: 100% auto, 0 auto;
    }
  }
  &--small{
    label:before{
      background-image: url(../img/check3.svg), url(../img/check2.svg);
    }
  }
  &--big{
    label{
      min-height: 1.625em;
      &:before{
        width: 1.625em;
        height: 1.625em;
      }
    }
  }
}

.m-radio {
  input {
    &:checked+label {
      &::before {
        background-size: 0 auto , 100% auto;
      }
    }
  }
  label {
    padding-top: 0;
    padding-left: 2.125em;
    display: flex;
    align-items: center;
    &:before {
      background-image: url(../img/radio.svg),url(../img/radio2.svg);
      background-size: 100% auto, 0 auto;
      width: 1.5em;
      height: 1.5em;
      top: 50%;
      transform: translateY(-50%);
      border: none;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 6.5em;
  height: 2.125em;
  position: relative;
  input{
    background-color: $fon;
    border: none;
    color: $black;
    font-size: .875em;
    text-transform: uppercase;
    text-align: center;
    width: 100%;
    height: 100%;
    border-radius: .5em;
    padding: 0 2.5em;
  }
  img{
    width: 1em;
    height: auto;
  }
  button{
    @include center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .ccalc-minus{
    left: .75em;
  }
  .ccalc-plus{
    right: .75em;
  }
}


