@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.75em;
    border-radius: .75em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    svg,
    img{
        width: 1.25em;
        margin: 0 .625em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}

.m-btn-small{
    height: 2.875em;
    padding: 0 1.5em;
    span{
        font-size: .875em;
    }
    &-arrow{
        padding-right: 1em;
    }
}

.m-btn-medium{
    padding: 0 1.5em;
    height: 3.25em;
    span{
        font-size: .875em;
    }
}

.m-btn-arrow{
    padding: 0 .875em 0 1.5em;
    height: 3.25em;
    img{
        margin-left: .5em;
    }
    span{
        font-size: .875em;
    }
}

.m-btn-green{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary, 10%);
        color: $white;
    }
}

.m-btn-green-outline{
    border: 1px solid $primary;
    color: $black;
    &._white{
        color: $white;
        &:hover{
            color: $white;
        }
    }
    &:hover{
        background-color: rgba($primary, .25);
        color: $black;
    }
}

.m-btn-gray2,
.m-btn-gray{
    background-color: $gray;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-gray2{
    background-color: $fon;
}

.m-btn-gray-outline{
    border: $border;
    color: $primary;
    &:hover{
        background-color: rgba($primary,10%);
        color: $primary;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: lighten($primary, 30%);
        color: $black;
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        background-color: $white;
        border-color: $white;
        color: $primary;
    }
}

.text-btn{
    font-weight: 600;
    color: $primary;
    display: flex;
    align-items: center;
    &:hover{
        color: $primary;
        opacity: .8;
    }
}

.round-btn{
    @include center;
    width: 2.375em;
    height: 2.375em;
    border-radius: 50%;
    img{
        width: 1em;
    }
}

.dots-link{
    display: flex;
    //color: $secondary;
    text-transform: uppercase;
    font-weight: 700;
    span{
        //border-bottom: 1px dotted $secondary;
    }
    &:hover{
        color: $primary;
        span{
            border-color: $primary;
        }
    }
    &._yellow{
        color: $primary;
        span{
            border-color: $primary;
        }
        &:hover{
            opacity: .8;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.375em;
        span{
            font-size: .875em;
        }
    }
}


@media screen and (max-width: 576px){
    ._full-mob{
        width: 100%;
    }
}
