.slider{
  position: relative;
  .swiper-container{
    width: calc(100% + 1.5em);
  }
  .swiper-slide{
    height: auto;
    padding-right: 1.5em;
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-button{
    margin: 0;
    position: static;
  }
  .swiper-nav{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: 2em;
  }
  &__wrap{
    overflow: hidden;
  }
}

.swiper-nav{
  display: flex;
  gap: .5em;
}

.swiper-button{
  width: 2.5em;
  height: 2.5em;
  background-color: $fon;
  @include center;
  border-radius: .5em;
  &:hover{
    opacity: .8;
  }
  img{
    width: .75em;
  }
  &.swiper-button-disabled{
    opacity: .5;
  }
}

.reviews-slider{
  .swiper-slide{
    width: 25%;
  }
}

.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.125em;
    margin-right: .125em;
    svg {
      width: 100%;
      height: auto;
    }
    path {
      fill: $gray;
      stroke: $gray;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: #F8B808;
      }
      &~.rating-input__item {
        path {
          fill: #F8B808;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: #F8B808;
    stroke: #F8B808;
  }
}

.review-card{
  display: flex;
  flex-direction: column;
  padding: 1.5em;
  background-color: $fon;
  border-radius: 1.5em;
  &__img{
    width: 2.5em;
    margin-right: .75em;
    flex-shrink: 0;
  }
  .rating{
    margin-right: .5em;
  }
  &__text{
    height: 6.875em;
    overflow: hidden;
    .text{
      font-size: .875em;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
  }
  &__product{
    border-radius: .625em;
    background: $white;
    padding: .5em 1em;
    &-text{
      @include truncate(1);
    }
  }
  &__images{
    display: flex;
    flex-wrap: wrap;
    gap: .5em;
  }
  &__img2{
    border-radius: .625em;
    width: 4em;
    height: 4em;
    @include imgCover(0);
  }
}


@media screen and (max-width: 992px){
  .reviews-slider{
    .swiper-slide{
      width: 50%;
    }
  }
}


@media screen and (max-width: 576px){
  .slider{
    .swiper-nav{
      margin-bottom: 1.5em;
    }
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      padding-right: .75em;
    }
  }

  .reviews-collection{
    .h2{
      padding-right: 4em;
    }
  }

  .reviews-slider{
    .swiper-slide{
      width: 19.5em;
    }
  }

  .review-card{
    padding: 1.5em;
    &__text{
      height: 5em;
    }
  }
}
