.text{
  @include grid100gap(1em);
  ._bold{
    li:before{
      font-weight: 700;
    }
  }
  ul,ol{
    padding-left: .625em;
    li{
      position: relative;
      padding-left: .875em;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  ol {
    counter-reset: list;
    padding-left: 0;
    li {
      counter-increment: list;
      padding-left: 1.125em;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul:not(.m-list){
    li{
      &:before{
        content: 'â€¢';
      }
    }
  }
  hr{
    display: block;
    opacity: 0;
    margin: -1em 0 0;
    height: 0;
  }
  img{
    display: block;
    width: 100%;
    border-radius: 1.5em;
  }
  h1,h2,h3,h4,h5,
  h6{
    color: $black;
  }
}



@media screen and (max-width: 992px){
  .text{
    p, ol li, ul  li{
      font-size: .875em;
      line-height: 157%;
    }
    ul:not(.m-list){

    }
  }
}


@media screen and (max-width: 576px){
  .text{
    hr{
      height: 0;
    }
  }
}
