.m-section{
  padding: 2em 0;
  &._first{
    padding-top: 0;
  }
  &._last{
    padding-bottom: 0;
  }
}

.full-block{
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  border-radius: 1.5em;
  @media screen and (min-width: 993px){
    width: calc(100% - 1.75em);
    .container{
      padding: 0 2.875em;
    }
  }
  .absolute-img{
    border-radius: 1.5em;
    z-index: -1;
  }
}

.consult{
  color: $white;
  .full-block{
    padding: 6.5em 0;
    background-color: $black;
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
    max-width: 42.5em;
  }
  &__btn{
    ._view2{
      display: none;
    }
  }
}

.faq{
  &__items{
    @include grid100gap(1em);
  }
}

.ceo{
  &:not(._show-all){
    .text{
      &>*:nth-child(5)~*{
        display: none;
      }
    }
  }
  &._show-all{
    .ceo__more{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
  }
  .text{
    max-width: 84em;
  }
  &__more{
    display: flex;
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.75em 0;
  }

  .consult{
    .full-block{
      padding: 3.5em 0;
    }
    &__content{
      margin-top: 1em;
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .m-section{
    padding: 1.5em 0;
    &._hidden-mob{
      overflow: hidden;
      .slider__wrap{
        overflow: unset;
      }
    }
  }

  .full-block{
    border-radius: 1em;
    .absolute-img{
      border-radius: 1em;
    }
  }

  .consult {
    .full-block {
      padding: 3em 0;
      min-height: 36.25em;
    }
  }

  .faq{
    &__items{
      gap: .75em;
    }
  }
}
