@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt80{
  margin-top: 5em;
}

._mt48{
  margin-top: 3em;
}

._mt40{
  margin-top: 2.5em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._mt20{
  margin-top: 1.25em;
}

._mt16{
  margin-top: 1em;
}

._mt12{
  margin-top: .75em;
}

._mt10{
  margin-top: .625em;
}

._mt8{
  margin-top: .5em;
}

._mt4{
  margin-top: .25em;
}

._gap16{
  gap: 1em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.50) 100%);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    @include lastM(0 1em 0 0);
    @include center;
    img{
      width: 100%;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-10%);
      }
    }
  }
  &--shops{
    .socials__item{
      width: 1.75em;
      height: 1.75em;
      @include lastM(0 .375em 0 0);
    }
  }
}



.qa-card{
  border-radius: 1.5em;
  background-color: $fon;
  &:last-child{
    border-bottom: none;
  }
  &._active{
    .qa-card__btn {
      path{
        stroke: $primary;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2em;
  }
  &__btn{
    margin-left: 1em;
    width: 2.5em;
    height: 2.5em;
    @include center;
    border-radius: .5em;
    background-color: $white;
    svg{
      width: .75em;
      transition: all .3s;
    }
  }
  &__hidden{
    margin-top: -.25em;
    padding: 0 2em 2em;
    display: none;
  }
  .text{
    max-width: 82em;
  }
}

.gray-bg{
  //background-color: $bg;
}

.opacity-40{
  opacity: .4;
}


@media screen and (max-width: 992px){
  ._mt64-tablet{
    margin-top: 4em;
  }

  ._mt40-tablet{
    margin-top: 2.5em;
  }

  ._mt32-tablet{
    margin-top: 2em;
  }

  ._mt24-tablet{
    margin-top: 1.5em;
  }

  ._mt20-tablet{
    margin-top: 1.25em;
  }

  ._mt16-tablet{
    margin-top: 1em;
  }

  .qa-card{
    &__hidden{
      margin-top: -.5em;
    }
  }
}


@media screen and (max-width: 576px){
  ._mt48-mob{
    margin-top: 3em;
  }

  ._mt32-mob{
    margin-top: 2em;
  }

  ._mt24-mob{
    margin-top: 1.5em;
  }

  ._mt16-mob{
    margin-top: 1em;
  }

  ._mt12-mob{
    margin-top: .75em;
  }

  ._mt8-mob{
    margin-top: .5em;
  }

  ._mt0-mob{
    margin-top: 0;
  }

  .qa-card{
    &__show{
      padding: 1.5em;
    }
    &__hidden{
      margin: 0;
      padding: 0 1.5em 1.5em;
    }
  }
}
