.pagination{
  margin-top: 2em;
  display: flex;
  justify-content: center;
  ul{
    display: flex;
    justify-content: center;
  }
  li{
    margin: 0 .375em;
  }
  .page-numbers,
  a{
    font-size: .875em;
    font-weight: 700;
    height: 3.43em;
    min-width: 3.43em;
    padding: 0 .5em;
    @include center;
    border: $border;
    border-radius: .6em;
    &:hover{
      color: $primary;
    }
  }
  .current a{
    background-color: $primary;
    border-color: $primary;
    color: $white;
    pointer-events: none;
  }
  .prev,
  .next{
    @include bgImgSize(.75em);
    border: none;
    a{
      background-color: transparent;
    }
  }
  .prev{
    background-image: url(../img/prev.svg);
  }
  .next{
    background-image: url(../img/next.svg);
  }
}

.more-btn{
  width: 100%;
  margin-top: 2em;
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .more-btn,
  .pagination{
    margin-top: 1.5em;
  }

  .pagination{
    ul{

    }
    li{
      margin: 0 .25em;
    }
    .page-numbers,
    a{
      font-size: .75em;
    }
    ._hide-mob{
      display: none;
    }
  }
}
