.footer{
  padding: 2em 0 1.5em;
  &__logo{
    display: block;
    width: 7.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__middle{
    display: flex;
    justify-content: space-between;
    padding-top: 3em;
    margin-top: 1.5em;
    border-top: $border;
    &-col2{
      flex-grow: 1;
      margin: 0 11.5em;
    }
  }
  &__bottom{
    margin-top: 3em;
    padding-top: 1.5em;
    border-top: $border;
    gap: .5em 2em;
  }
  &__link{
    margin-left: auto;
  }
}

.footer-nav{
  display: flex;
  gap: 1.5em;
  ul{
    min-width: 8em;
  }
  li{
    margin-bottom: .75em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.footer-contacts{
  display: flex;
  gap: 3em;
}

.footer-contacts2{
  &__btn{
    margin-left: 2.5em;
  }
}

.footer-socials{
  .socials{
    margin-left: 1em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 1.75em;
    &__middle{
      top: 2em;
      align-items: center;
      flex-wrap: wrap;
      &-col2{
        order: 2;
        width: 100%;
        margin: 2em 0 0;
      }
    }
    &__bottom{
      margin-top: 2em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin-left: 0;
    }
  }

  .footer-contacts{
    gap: 1.5em;
  }

  .footer-contacts2{
    &__btn{
      margin-left: 1.5em;
    }
  }

  .footer-nav{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    ul{
      min-width: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-top: 1.5em;
    &__top{
      display: block;
    }
  }

  .footer-contacts{
    display: grid;
    grid-template-columns: 100%;
  }

  .footer-contacts2{
    margin-top: 1.5em;
    justify-content: space-between;
  }

  .footer-nav{
    display: block;
    column-count: 2;
    column-gap: .75em;
    ul{
      display: inline-block;
      width: 100%;
      margin-bottom: .75em;
    }
  }

  .footer-socials{
    .body-s{
      display: none;
    }
  }
}
