._dense{
  grid-auto-flow: dense;
}

.grid,
.grid2,
.grid3,
.grid4,
.grid5,
.grid6{
  display: grid;
  gap: 1.5em;
}

.grid{
  grid-template-columns: 100%;
}

.grid2{
  grid-template-columns: repeat(2,calc(50% - .75em));
}

.grid3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
}

.grid4{
  grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
}

.grid5{
  grid-template-columns: repeat(5,calc(20% - 6em / 5));
}

.grid6{
  grid-template-columns: repeat(6,calc(100% / 6 - 7.5em / 6));
}


@media screen and (max-width: 992px){
  .grid-tablet{
    grid-template-columns: 100%;
  }

  .grid-tablet2{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }

  .grid-tablet3{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  }

  .grid-tablet4{
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
  }
}


@media screen and (max-width: 576px){
  .grid-mob{
    grid-template-columns: 100%;
  }

  .grid-mob-small{
    grid-template-columns: 100%;
    gap: .75em;
  }

  .grid-mob-small2{
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
}
