.produce{
  .full-block{
    padding: 4em 0 1.5em;
  }
  &__top{
    max-width: 55.5em;
    color: $white;
  }
  &__bottom{
    margin-top: 10em;
  }
  @media screen and (min-width: 993px){
    .advantage-card{
      background-color: $white;
      &__icon{
        background-color: $fon;
      }
    }
  }
}

.absolute-img{
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include imgCover(0);
  position: absolute;
}

.m-card{
  padding: 2em;
  background-color: $fon;
  border-radius: 1.5em;
}

.m-icon{
  @include center;
  width: 4.5em;
  height: 4.5em;
  border-radius: .75em;
  background-color: $white;
  img{
    width: 1.5em;
  }
}

.advantage-card{
  &__icon{
    margin-bottom: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .produce{
    .container{
      padding: 0;
    }
    .full-block{
      padding: 0;
    }
    &__top{
      max-width: 100%;
      padding: 3.5em 1.875em;
      min-height: 32.5em;
      position: relative;
    }
    &__bottom{
      margin-top: 1.5em;
      padding: 0 1.875em;
    }
  }

  .advantages{
    &__grid{
      .advantage-card{
        &:last-child{
          grid-column: 1/-1;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .produce{
    &__top{
      padding: 3em 1em;
    }
    &__bottom{
      padding: 0 1em;
    }
  }

  .m-card{
    padding: 1.5em;
    border-radius: 1em;
  }

  .m-icon{
    width: 3.5em;
    height: 3.5em;
  }
}
