:root {
    --vh: 1vh;
}

$font1:'Manrope', sans-serif;
$font2:'Montserrat', sans-serif;
$black: #2c332f;
$white: #fff;
$fon: #f5f5f5;
$primary: #5ab179;
$line: #e3e3e3;
$gray: #8d8f91;
$vh: calc(100 * var(--vh));

$border: 1px solid $line;
$shadow: 0 6px 16px 0 rgba(#141414, 0.10);

